  /* Ensure that elements intended to be hidden are not displayed, improving clarity and layout control. */
  display: none !important;
}

.c-accordion {
  border-radius: 9px;
  overflow: hidden;
  border: 2px solid var(--base-color);
}

.c-accordion__btn {
  display: block;
  width: 100%;
  margin: 0;
  padding: 0;
  border: 0;
  background: transparent;
  font-size: calc(19 / 16 * 1rem);
  color: var(--white);
  background: var(--base-color);
  padding: calc(23 / 16 * 1rem) calc(75 / 16 * 1rem) calc(23 / 16 * 1rem) calc(47 / 16 * 1rem);
  transition: background var(--duration), opacity var(--duration);
  text-align: left;
  position: relative;
}
@media screen and (max-width: 767px) {
  .c-accordion__btn {
    font-size: calc(17 / 16 * 1rem);
    padding: calc(15 / 16 * 1rem) calc(45 / 16 * 1rem) calc(15 / 16 * 1rem) calc(20 / 16 * 1rem);
  }
}
@media (any-hover: hover) {
  .c-accordion__btn:hover {
    opacity: 0.7;
  }
}
.c-accordion__btn::before, .c-accordion__btn::after {
  content: "";
  display: block;
  width: calc(27 / 16 * 1rem);
  height: 3px;
  background: currentColor;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  right: calc(45 / 16 * 1rem);
  transform-origin: center center;
  transition: transform var(--duration);
}
@media screen and (max-width: 767px) {
  .c-accordion__btn::before, .c-accordion__btn::after {
    right: calc(20 / 16 * 1rem);
    width: calc(20 / 16 * 1rem);
  }
}
.c-accordion__btn::before {
  transform: translate(0%, -50%) rotate(90deg);
}
.c-accordion__btn::after {
  transform: translate(0%, -50%) rotate(0deg);
}
.c-accordion__btn[aria-expanded=true]::before {
  transform: translate(0%, -50%) rotate(0deg);
}

.c-accordion__body {
  display: grid;
  grid-template-rows: 0fr;
  transition: 250ms grid-template-rows ease, 250ms padding-block ease;
  padding-inline: calc(47 / 16 * 1rem);
  background: var(--white);
}
@media screen and (max-width: 767px) {
  .c-accordion__body {
    padding-inline: calc(20 / 16 * 1rem);
  }
}
.c-accordion__body > div {
  overflow: hidden;
}
.c-accordion__body[aria-hidden=false] {
  grid-template-rows: 1fr;
  padding-block: calc(30 / 16 * 1rem);
}
@media screen and (max-width: 767px) {
  .c-accordion__body[aria-hidden=false] {
    padding-block: calc(15 / 16 * 1rem);
  }
}

.c-accordion__answer {
  padding-left: 1.5em;
  position: relative;
}
.c-accordion__answer::before {
  content: "A.";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
}

.c-btn {
  inline-size: fit-content;
  outline-offset: 2px;
  touch-action: manipulation;
  user-select: none;
}
.c-btn:focus-visible {
  outline-offset: 2px;
}

.c-hamburger {
  --hamburger-color1: var(--white);
  --hamburger-color2: var(--white);
}
@media screen and (max-width: 767px) {
  .c-hamburger {
    display: inline-block;
    text-decoration: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    border: none;
    background: var(--black);
    cursor: pointer;
    -webkit-transition: all 0.3s ease-in-out;
    transition: background 0.3s ease;
    position: relative;
    width: calc(50 / 16 * 1rem);
    height: calc(50 / 16 * 1rem);
    border-radius: 50%;
  }
}

@media (hover: hover) {
  .c-hamburger:hover {
    background: var(--black);
  }
  .c-hamburger:hover .c-hamburger__line {
    background: var(--hamburger-color2);
  }
  .c-hamburger:hover[aria-expanded=true] .c-hamburger__line {
    background: transparent;
  }
}
.c-hamburger__line {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: calc(20 / 16 * 1rem);
  height: 1px;
  background: var(--hamburger-color1);
  transition: background 0.3s ease;
}

.c-hamburger__line::before,
.c-hamburger__line::after {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  background: inherit;
  content: "";
  transition: transform 0.3s ease;
}

.c-hamburger__line::before {
  top: calc(-6 / 16 * 1rem);
}

.c-hamburger__line::after {
  top: calc(6 / 16 * 1rem);
}

.c-hamburger[aria-expanded=true] {
  background: var(--black);
}
.c-hamburger[aria-expanded=true] .c-hamburger__line {
  background: transparent;
}
.c-hamburger[aria-expanded=true] .c-hamburger__line::before, .c-hamburger[aria-expanded=true] .c-hamburger__line::after {
  top: 0;
  background: var(--hamburger-color2);
}
.c-hamburger[aria-expanded=true] .c-hamburger__line::before {
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}
.c-hamburger[aria-expanded=true] .c-hamburger__line::after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.c-modalMedical {
  background: var(--white);
  border: 5px solid var(--base-color);
  border-radius: 21px;
  width: min(1200px, 90vw);
  height: 80vh;
  margin: auto;
}

.c-modalMedical[open] {
  display: block;
}

.c-modalMedical {
  box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.5);
  transition: opacity 0.5s;
}

.c-modalMedical__container {
  height: inherit;
  block-size: 100%;
  position: relative;
}

.c-modalMedical__inner {
  padding: calc(73 / 16 * 1rem) calc(30 / 16 * 1rem);
  height: inherit;
  overflow: scroll;
  overscroll-behavior: contain;
}
